<script>
    import leftsearch from '../../../components/Common/left-search.vue';
    import Schema from "../../CRM/InWard/InwardListingSchema";
    import InwardDataService from "../../../services/DataService";
    import modalfollowupslist from '../../../components/Common/Modal/modalfollowupslist.vue';
    import modalconvertlead from '../../../components/Common/Modal/modalconvertlead.vue';
    import  viewlayout from '../../../components/Common/Modal/view-layout.vue';
    import searchfilter from '../../../components/Common/Modal/search-filter.vue';
    import modaldeploy from '../../../components/Common/Modal/modaldeploy.vue';
export default {
    components:{
        leftsearch,
        modalfollowupslist,
        modalconvertlead,
        viewlayout,
        modaldeploy,
        searchfilter
    },
    data() {
        return{
      isLoading: false, 
        appliedFilter: [],
        SelectedGinwards: '',
        selectedGinVendor: [],
        GinVendorNameist: null,
        ShowManageView: false,
        options: null,
        LegendArray: [],
        DependentOption: [{
            format:'MM-dd-yyyy',
            useCurrent: false,
            showClear: true,
            showClose: true,
            allowInputToggle: true,
            minDate: new Date(Date.now())
        }
        ],
        FromDateForSearch: '',
        ToDateForSearch: '',
        txtGINnumber: '',
        InWardData: null,
        Headers: null,
        TotalRecords: 0,
        PageNumber: 1,
        PageSize: 10,
        SortBy: '',
        SortExp: '',
        PageSizeOption: null,
        CurrentPage: 1,
        TotalPages: 0,
        GroupStart: 1,
        GroupEnd: 3,
        showAddEventModal: false,
        stageName: '',
        langCode: 'EN',          
        noRecord: false,
        showFilter: false,
        searchCondition: '',
        showAddEmailTemplate: false,
        showDeployModal: false,
        modalItem: [],
        LocationSelect: null,
        ddlinwards: '',
        ginid: null,
        IsAnySearch: false,
        StatusListForListing: [],            
        conditionForInstantSearch: "",
        listheaderbuttons: Schema.buttons,
        leftSearchFields: Schema.leftsearchSchema,
        listActions: Schema.listActions,        
        usertype:null,
        LoggedinUserId:null,
        privilegeParams: null,
        convertLeadValue: { value: null, name: '' },
        showConvertLead: false,
        ViewList:[],
        ViewListId:''
    }
    },
    created: function () {
        var ths = this;
        ths.LoggedinUserId = ths.GetUserInfo.ID;
        ths.usertype = ths.GetUserInfo.UserType;
        ths.getStatuslistForListing('CRM', 'en');
        ths.privilegeParams = `controller=CRM&action=`;
        ths.GetGinVendorlist(); 

            ths.FetchData();
            ths.GetDdlRecords_ViewList();
     
    },
    methods: {
        GetDdlRecords_ViewList: async function () {
            var vm = this;
            vm.ViewList=[];
            var url = `moduleName=CRM&subModuleCode=CRM_INWARD&viewId=0&pageSize=100&pageNum=1&sortBy=&sortExp=`;
            await InwardDataService.GetViewList(url).then(function (response) {               
                if(response.data && typeof(response.data.DATA) != 'undefined'){
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {
                   
                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if( vm.ViewListId > 0){
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;    
                    }else{
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);  
                                
            });           
        }, 
        DefaultView: function () {  
                                            
            var vm = this;
            var obj = {
                ModuleName: 'CRM',
                SubmoduleCode: 'CRM_INWARD'
            };            
            var objectPostString = JSON.stringify(obj);
            InwardDataService.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ClearSuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        //vm.$emit('RefreshManageView');
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {  
                    
            var vm = this;
            if (viewId == '0') {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'CRM',
                    SubmoduleCode: 'CRM_INWARD'
                };
                var objectPostString = JSON.stringify(obj);                     
                InwardDataService.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
    renderActions: function(row) {
    var self = this;
    let rtnActionArray = [];
    self.listActions.forEach(actionItem => {
        let action = {...actionItem};        
        switch (action.title) {            
            case 'FollowUps':
            action.isVisible = row.status_id != 'Rejected' && row.status_id != 'Approved' && row.status_id != 'Closed'
                break;
                case 'Edit':
                        action.isVisible = row.status_id == 'Active';
                        break;
                case 'Approve':
                    action.isVisible=row.status_id != 'Rejected' && row.status_id != 'Approved' && row.status_id != 'Closed'
                break;
                case 'Reject':
                    action.isVisible=row.status_id != 'Rejected' && row.status_id != 'Approved' && row.status_id != 'Closed'
                break;
                case 'ConvertLead':                    
                    action.isVisible=((self.isBlank(row.existing_client_id) || self.isBlank(row.project_id)) && row.is_completed==1 );
                break;
              default:
                action.isVisible = true;
                break;            
        }
        rtnActionArray.push(action);
    });
    return rtnActionArray;
        },
        clickEventOfColumn:function(row, column){
    if(column.COLUMN_NAME == 'gin_number'){
        this.$router.push({ path: '/CRM/ViewInwardDetails/'+row.gin_id+'/CRM/CRM_INWARD' });
    }
        },
        commonHeaderButtonClick:function(event){
            switch (event.callbackfunction) {
                case "downloadExceldata":
                    this.FetchData(true);
                    break;                  
                case "ManageView":
                  this.ManageView();
                    break;                                   
                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                    case "openlocation":
                    this.openlocation(null);
                    break;  
                 case "commonReject" :
                    this.commonReject(null);
                    break;                         
                    case "commonDeleteData":
                    this.commonDeleteData();
                    break;  
                case "removeFilter":
                    var ths = this;
                    this.searchCondition = '';
                    const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'removeFilter';});
                    ths.listheaderbuttons[removeFilterindex].isvisible=false;
                    const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'openFilterPopUp';});
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                     try {
                        ths.appliedFilter = [];
                     } catch (e) { }
                     ths.FetchData();
                    break; 
                    case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                        break;
            }
        },
      
       
        onChange: function () {               
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('.statuschange').removeClass('disabled');
            }
            else {
                $('.statuschange').addClass('disabled');
            }
        },
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.FetchData();
        },
        DependentDateObject: function (date) {
            var checkDate = new Date();
            if ((typeof date !== 'undefined') && date != '') {
                checkDate = new Date(Date.parse(date));
            }
            else {
                checkDate = new Date(Date.now());
            }
            this.DependentOption[0] = {
                format: 'MM-dd-yyyy',
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: checkDate
            }
        },
        InwardChange: function (event) {               
            var value = event.target.value;
            var vueObj = this;
            vueObj.searchCondition = '',
            vueObj.ddlinwards = value;
        },
        SetCurrentPage: function (pageNumber) {
            this.PageNumber = pageNumber;
            if (pageNumber > this.GroupEnd || pageNumber == 1) {
                this.GroupStart = pageNumber;
                this.GroupEnd = pageNumber + 2;
            }
            else if (pageNumber < this.GroupStart && pageNumber > 1) {
                this.GroupEnd = this.GroupStart - 1;
                this.GroupStart = pageNumber - 2;
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
           
           this.PageSize = value.PageSize;
           this.PageNumber = value.PageNumber;
           this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
       this.SortBy = s;
            if (s === this.SortBy) {
             
                if (this.SortExp == "") {
                this.SortExp = "ASC";
                SortDir = "sort tb_headerSortDown";
            }
            else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
                SortDir = "sort tb_headerSortUp";
            }
            else
            {
                this.SortExp = "ASC";
                SortDir = "sort tb_headerSortDown";
            }
            }
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
            $("#" + s).attr("class", SortDir);
           
          
        },
        SetPageSize: function (event) {
            this.PageSize = event.target.value;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        cancelOrRefresh: function () {
            this.SelectedGinwards = '';
            this.selectedGinVendor = [];
            this.ddlinwards = '';
            this.txtGINnumber = '';
            this.FromDateForSearch = '';
            this.ToDateForSearch = '';
            this.searchCondition = '';
            this.IsAnySearch = false;
            this.stageName = "";
            this.PageNumber = 1;
            this.clientemail = "";
            this.selectedLeadOwner = "";
            this.NameSearch = "";
            this.CompanyName = "";
            this.hdnOwnerId = "";
            this.conditionForInstantSearch = "";
            this.IsAnySearch = false;
            this.FetchData();
           
        },
        ClearFilter: function (prop) {
            
            var vm = this;
            if (prop == 'SelectedGinwards') {
                vm.SelectedGinwards = '';
                vm.ddlinwards = '';
            }
            else if (prop == 'selectedGinVendor') {
                vm.selectedGinVendor = '';
            }
            vm.searchCondition = '';
            vm.FetchData();
        },
        GetGinVendorlist: function () {
            var vm = this;
            var url = "id=&moduleName=CRM&type=VENDOR_NAME";
             
            InwardDataService.getDDLData(url).then(function (response) {
                    var json = response.data;
                     
                    if (json != null) {
                        vm.leftSearchFields[2].listOptions = json.DATA;
                        vm.GinVendorNameist = json.DATA
                        return vm.GinVendorNameist;
                    }
                     
                });
        },
    
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: function (downloadExceldata) {
            this.isLoading = true;
            var vm=this;
            var param ;
            if(downloadExceldata){
                param = "pageSize=" + vm.TotalRecords + "&pageNumber=" + vm.PageNumber + "&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&stagename=" + vm.stageName + "&langCode=" + vm.langCode;          
            }else{
                param = "pageSize=" + vm.PageSize + "&pageNumber=" + vm.PageNumber + "&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&stagename=" + vm.stageName + "&langCode=" + vm.langCode;          
            }
            var srchCondition = "";
            if (!vm.isBlank(vm.searchCondition)) {
                srchCondition = srchCondition + vm.searchCondition;
            }
            if (!vm.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!vm.isBlank(this.searchCondition)) ? (" AND " + vm.conditionForInstantSearch) : vm.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                param+="&searchCondition="+srchCondition; 
                if(vm.appliedFilter.length>0){
                const removeFilterindex= vm.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vm.listheaderbuttons[removeFilterindex].isvisible=true;      
                const openFilterPopUpindex= vm.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vm.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
                }
            }
            
            if(downloadExceldata){
                InwardDataService.GoodInwardListing(param).then(function (response) {
                    vm.isLoading = false;
                    if (response.data != null) {
                        vm.JSONToCSVConvertor(response.data, "Inward-Detail", true);
                    }
                    else {
                        vm.ModelnoRecord = true;
                    }
                })
            }else{
                InwardDataService.GoodInwardListing(param).then(function (response) {
                          if (response.data != null && response.data != "") {
                            vm.InWardData = [];
                            vm.Headers = response.data.schema;
                            vm.Headers.forEach((column) => {
                                column.DISPLAY_NAME= vm.$t(column.DISPLAY_NAME ? column.DISPLAY_NAME : column.COLUMN_NAME);
                                column.settings = null;
                                if(column.COLUMN_NAME == 'gin_number'){
                                    column.settings = {
                                        clickEvent: vm.clickEventOfColumn,
                                        formatter: vm.columnDataFormatter
                                    }
                                }
                                if(column.COLUMN_NAME == 'status_id'){
                                    column.settings = {
                                        isInSlot : true
                                    }
                                }
                            });
                              if (response.data.data.length > 0) {
                                  vm.InWardData = response.data.data;
                                  vm.InWardData.forEach((row)=>{
                                    row.isCheckBoxDisabled=false;
                                    if(row.status_id=='Closed' || row.status_id=='Partially Approved' || row.status_id=='Partially Rejected'
                                    || row.status_id=='Approved' || row.status_id=='Rejected'){
                                        row.isCheckBoxDisabled=true;
                                    }
                                    row.rowDetailsUrl='';
                                    if(row.rowDetailsUrl=='')
                                    {
                                        row.rowDetailsUrl='/CRM/ViewInwardDetails/'+row.gin_id+'/CRM/CRM_INWARD';
                                    }
                                  })
                                  vm.TotalRecords = response.data.data[0].total_records;
                                  vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                                  vm.CurrentPage = vm.PageNumber;
                                  $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                  $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                  $('#DeleteStage').addClass('disabled');
                                  $('#Sendbulkmail').addClass('disabled');
                                  vm.GetDdlRecords_ViewList();
                                  vm.noRecord = false;
                              } else {
                                vm.noRecord = true;
                                vm.InWardData = [];
                                vm.TotalRecords = 0
                              }
                          }
                          else {
                            vm.noRecord = true;
                            vm.InWardData = [];
                            vm.Headers = [];
                            vm.TotalRecords = 0
                          }
                          vm.isLoading = false;
                      })
                    }
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
             
            InwardDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },           
        GetPageSizeOption: function () {
           
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
       
        closeEventViewModal: function () {
        this.showAddEventModal = false;
    },
        openAddNewEvent: function (source_id,item) {
                 var vm = this;
                if (item != null) {
                        vm.souceID = source_id;
                        vm.selectedItem = item;
                        setTimeout(function () {
                            vm.showAddEventModal = (vm.showAddEventModal) ? false : true;
                        }, 500);
                        
                } else {
                    vm.souceID = source_id;
                    vm.selectedItem = item;
                    setTimeout(function () {

                        console.log(vm.$refs.modalfollowupslist);
                        vm.showAddEventModal = (vm.showAddEventModal) ? false : true;
                    }, 300);
                }
            },
        openFilterPopUp(source_id) {
            this.cancelOrRefresh();
            this.showFilter = true;
        },
      
        openlocation: function (value) {            
            var vm= this;
            InwardDataService.CheckPrivilege(vm.privilegeParams + 'ApproveInward')
                .then(function (response) {
                    if (response.data) {
                        if ($(event.target).hasClass("disabled") || $(event.target).closest("a.iconenable").hasClass("disabled")) {
                            return false;
                        }
                        vm.ginid = value;
                       
                        vm.showDeployModal = (vm.showDeployModal) ? false : true;
                       
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }
        });
        },
        commonApprove: function (LocationSelect,itemId) { 
            debugger           
            this.LocationSelect = LocationSelect;
            if (this.LocationSelect != null) {
                var vm = this;
                var val = "";
                if (typeof (this.ginid) !== "undefined" && this.ginid != null) {
                    val = this.ginid;
                }
                else {
                    if(itemId != null){
                        val=itemId;
                    }else{
                        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                            if (val.length > 0)
                                val += ',';
                            val += $(this).val();
                        });
                    }
                    
                }
                if (val.length > 0 || val != "") {
                    vm.confirmR(vm.$t("AreYouSureToApproveSelectedRecords"), true, vm.$t("Approve",[vm.$t("GinInward")]), function () {
                        var postJSON = {
                            ids: val,
                            moduleName: "CRM",
                            subModuleCode: "CRM_INWARD",
                            approvaltype: 'CRM_APPROVE',
                            viewmode: '',
                            locationid: vm.LocationSelect
                        };
                        var objectPostString = JSON.stringify(postJSON);
                        InwardDataService.changestausInwards(objectPostString).then(function (response) {     
                            var box = "";
                            if (response.data[0].Status == "Success") {
                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                box = vm.ShowAlert(vm.$t('ApprovedSuccessfully', [vm.$t('GINInward')]), "success", true, vm.$t("Alert"));                                 
                                if(vm.showDeployModal==true){
                                    vm.showDeployModal=false;
                                }
                                vm.FetchData();
                                $('.statuschange').addClass('disabled');
                            }
                            else {
                               
                                box = vm.ShowAlert(vm.$t("Error Occur while Approving Record"),"warning", true, vm.$t("Alert"));
                            }
                             
                        }, function (response) {
                             
                        });
                    });
                }
                else {
                    vm.ShowAlert(vm.$t("LeaveApprove"),"warning", true, vm.$t("Alert"));
                }
            }
            else {
                vm.ShowAlert(vm.$t("Please Select the location in order to approve"),"warning", true,this.$t("Alert"));
            }
        },
        commonReject: function (id) {
            var vm=this;
            InwardDataService.CheckPrivilege(vm.privilegeParams + 'RejectInward')
                .then(function (response) {
                    if (response.data) {
                        if ($(event.target).hasClass("disabled") || $(event.target).closest("a.iconenable").hasClass("disabled")) {
                            return false;
                        }

                        var val = "";
                        if (typeof (id) !== "undefined" && id != null) {
                            val = id;
                        }
                        else {
                            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                                if (val.length > 0)
                                    val += ',';
                                val += $(this).val();
                            });
                        }
                        if (val.length > 0 || val != "") {
                            vm.confirmR(vm.$t("AreYouSureToRejectSelectedRecords"), true, (vm.$t("Reject") + "  " + vm.$t("GinInward")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_INWARD",
                                    approvaltype: 'CRM_REJECT',
                                    viewmode: '',
                                    locationid: ''
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                InwardDataService.changestausInwards(objectPostString)
                                    .then(function (response) {

                                        var box = "";
                                        if (response.data[0].Status == "Success") {
                                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                            box = vm.ShowAlert(vm.$t('RejectedSuccessfully', [vm.$t('GinNumber')]), "success", true, vm.$t("Alert"));
                                            vm.FetchData();
                                            $('.statuschange').addClass('disabled');
                                        }
                                        else {
                                            box = vm.ShowAlert(vm.$t('RejectError', [vm.$t('GinNumber')]), "failure", true, vm.$t("Alert"));
                                        }
                                        setTimeout(function () {
                                            box.modal('hide');
                                        }, 2000);

                                    }, function (response) {

                                    });
                            });
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("RemoveReject"), "warning", true, vm.$t("Alert"));
                        }
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }
        });
            
        },
        openConvertLeadViewModel: function (gin_id, name) {
            var vm = this;
            if(confirmR("Please verify, you want to convert this Lead?")) {                        
                 vm.convertLeadValue.value = gin_id;
                 vm.convertLeadValue.name = name;
                 vm.showConvertLead = (vm.showConvertLead) ? false : true;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'checkPrivilegeView':

                    break;
                case 'checkPrivilegeDetails':

                    break;
                case 'openlocation':
                    vm.commonApprove(item, item.gin_id);
                    break;
                case 'commonReject':
                    vm.commonReject(item.gin_id);
                    break;
                case 'openAddNewEvent':
                    vm.openAddNewEvent(item.gin_id, item);
                    break;
                case 'openConvertLeadModel':
                    var convertLeadHeader = ((item.client_first_name != null) ? item.client_first_name : '') + ' ' + ((item.client_last_name != null) ? item.client_last_name : '');
                    vm.openConvertLeadViewModel(item.gin_id, convertLeadHeader);
                    break;
            }
        },
        ManageView: function () {   
                var vm=this;         
                this.ShowManageView = true;
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        vm.SetBootstraptoolTip();
                    }, 200);
                }, 1000);          
            },
        commonDeleteData: function (event) {
                var vm = this;
                InwardDataService.CheckPrivilege(vm.privilegeParams + 'DeleteInwards')
                .then(function (response) {
                    if (response.data) {
                        var val = "";
                        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                            if (val.length > 0)
                                val += ',';
                            val += $(this).val();
                        });
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("GinInward")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_INWARD"
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                InwardDataService.CommonDeleteData(objectPostString).then(function (response) {
                                    var box = "";
                                    if (response.data[0].Status == "Success") {                                       
                                        box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('GinInward')]), "success", true, vm.$t("Alert"));                                        
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        vm.FetchData();
                                    }
                                    else {
                                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('GinInward')]), "failure", true, vm.$t("Alert"));
                                    }
                                    setTimeout(function () {
                                        vm.box.modal('hide');
                                    }, 2000);
                                });
                            });
                        }
                        else {
                            vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
                        }
                    }
                    else {
                        vm.ShowAlert('Sorry, You are not authorized to access this section.', "failure", true, "Alert");
                    }
        });
        }
    }
}

</script>
<template>   
<div><loader :is-visible="isLoading"></loader>
<tg-list :IsShowAction="true"  
:showCheckBox="true"
                ModuleName="CRM" 
                SubModuleCode="CRM_INWARD" 
                IdentityColumn="gin_id"
                :HeaderText="$t('Goods Inward')"
                :ListData="InWardData" 
                :HeaderData="Headers"
                v-on:ApplyView="OnApplyView" :ViewList="ViewList"
                ref="tgList" 
                :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecords"
                :SearchFields="leftSearchFields"
                :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="FetchData"
                :LegendArray="LegendArray"
                :noRecord="noRecord"
                :SortExp="SortExp"
                :SortBy="SortBy"
                :NorecordfoundText="$t('NoRecordfound')"
                :CurrentPageNumber="PageNumber"
                :IsFilterApplied="searchCondition?true:false"
                v-on:LeftsearchButtonClick="leftCommonSearch"
                v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:ActionButtonClick="actionButtonClick"
                v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{data}">
                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <listing-status  :isfor="'CRM_INWARD'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_INWARD')" ref="updatestatus" v-bind:dataid="data.row.gin_id" data-controller="Crm" 
                        v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index"  
                        v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData"
                         v-bind:isselectedvaluestring="true">
                        </listing-status>
                        <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_INWARD')">
                            <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                                {{data.row[data.column.COLUMN_NAME]}}
                            </span>
                        </a>
                    </template>
                </template>
        </tg-list>
        <modalfollowupslist :srcId="souceID" :DealInfo="selectedItem" v-if="showAddEventModal" modulename="CRM" submodulecode="CRM_INWARD" :userid="userid"
                     @close="closeEventViewModal">
    </modalfollowupslist>
    <modalconvertlead v-if="showConvertLead" @close="showConvertLead = false" :leadId="convertLeadValue.value" moduleName="CRM" subModuleCode="CRM_LEADS"> 
    </modalconvertlead>

    <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_INWARD"  modulename="CRM"  @close="ShowManageView = false" v-bind:callbackfunction="FetchData"  :usertype="usertype">
        <h5 slot="header">{{$t('Search')}}</h5>
    </viewlayout>
    <searchfilter 
                v-if="showFilter" 
                submodulecode="CRM_INWARD" 
                modulename="CRM" 
                :userid="userid" 
                :companyid="CompanyId" 
                v-on:close="showFilter = false" 
                v-bind:callbackfunction="FetchData"
                v-on:FilterData="FilterData">
                <h5 slot="header">{{$t('Search')}}</h5>
            </searchfilter>
    
        <modaldeploy v-if="showDeployModal" @close="showDeployModal = false">
                <h5 slot="header">{{$t('DeployItems')}}</h5>
            </modaldeploy>
        </div>
</template>
